%h1 {
  color: $clr-gray;
  font-family: $font-main;
  font-size: 40px;
  font-style: normal;
  font-weight: 700;
  line-height: 52px;
  text-transform: uppercase;

  @include on-tablet {
    font-size: 48px;
    line-height: 62px;
  }

  @include on-desktop {
    font-size: 62px;
    line-height: 78px;
  }
}

%h2 {
  color: $clr-gray;
  font-family: $font-main;
  font-size: 30px;
  font-style: normal;
  font-weight: 700;
  line-height: 40px;
  text-transform: uppercase;

  @include on-tablet {
    font-size: 36px;
    line-height: 46px;
  }

  @include on-desktop {
    font-size: 56px;
    line-height: 70px;
  }
}

%h3 {
  color: $clr-gray;
  font-family: $font-main;
  font-size: 22px;
  font-style: normal;
  font-weight: 700;
  line-height: 28px;
  text-transform: uppercase;
}

%h4 {
  color: $clr-gray;
  font-family: $font-main;
  font-size: 16px;
  font-style: normal;
  font-weight: 700;
  line-height: 22px;
}

%h5 {
  color: $clr-cyan-3;
  font-family: $font-main;
  font-size: 18px;
  font-style: normal;
  font-weight: 700;
  line-height: 22px;
  text-transform: uppercase;

  @include on-desktop {
    font-size: 34px;
    line-height: 42px;
  }
}

%nav-link {
  display: inline-block;

  color: $clr-gray;
  font-family: $font-main;
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 20px;

  transition: all 0.3s;

  &:hover {
    color: $clr-black;
    font-weight: 700;
  }
}

%input-form {
  color: $clr-gray;
  font-family: $font-secondary;
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  line-height: 24px;
}
